function enableKeybaord() {
    // 키보드 보안 적용할 필드
    var _Password = $('#qlalfqjsgh, #ask-qlalfqjsgh');
    _Password.off('keypress keydown');

    _Password.on('keypress', function(event) {
        event.preventDefault();

        var cursor = this.selectionStart;
        var temp = this.value;

        // 일정 텍스트 영역을 드래그하여 블록을 씌웠을 경우
        if (this.selectionStart !== this.selectionEnd) {
            // 블록된 부분 삭제
            temp = temp.substring(0, this.selectionStart) + temp.substring(this.selectionEnd);
            // 마찬가자로 블록된 부분의 내부 비밀번호 삭제
            webnpki.modifyPassword(this.selectionStart, this.selectionEnd, 8, false);
        }

        // 가짜 값 생성
        this.value = temp + String.fromCharCode(Math.floor(Math.random(1, 10) * 110) % 83 + 33);

        // 커서 위치를 블록 영역 끝으로 이동
        if (this.setSelectionRange) {
            this.setSelectionRange(cursor + 1, cursor + 1);
        } else {
            var range = this.createTextRange();
            range.collapse(true);
            range.moveEnd('character', cursor + 1);
            range.moveStart('character', cursor + 1);
            range.select();
        }

        // 비밀번호 암호화 진행                       
        webnpki.setPassword(this.selectionStart, event.which || event.keyCode);
    })

    _Password.on('keydown', function(event) {
        // 이벤트키를 누르고 있는 경우
        if (event.altKey || event.shiftKey || event.metaKey) {
            return;
        }

        var keycode = event.which || event.keyCode;
        // Backspace랑 Delete키 입력이고 가상키보드를 열어놓지 않은 경우
        if ((keycode === 46 || keycode === 8) && !$("#" + this.id).getkeyboard()) {
            // 내부 비밀번호 값 변경
            webnpki.modifyPassword(this.selectionStart, this.selectionEnd, keycode, event.ctrlKey);
        }
    });
};

module.exports = {
    enableKeybaord: enableKeybaord
}